	module.exports = {"data":{"markdownRemark":{"html":"<p>Làm trong các công ty phần mềm, bạn sẽ nghe đi nghe lại mấy chữ này <strong>Scrum</strong>, <strong>Agile</strong>, <strong>planning poker</strong>, <strong>Stand up</strong>, <strong>Sprint</strong>. Nó là những thuật ngữ được dùng ám chỉ một <strong>quy cách tổ chức công việc, xét độ ưu tiên, và phân phối</strong> những công việc này giữa các thành viên trong team.</p>\n<h1 id=\"agile-và-scrum\"><a href=\"#agile-v%C3%A0-scrum\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Agile và Scrum</h1>\n<p>Agile và Scrum hay đi chung, nhưng nó khác nhau (không lớn lắm).</p>\n<ul>\n<li>\n<p><strong>Agile</strong> là phương pháp được sáng tạo bởi Agile Manifesto, nó là lý thuyết nền tảng</p>\n</li>\n<li>\n<p><strong>Scrum</strong> là một framework hiện thực hóa từ đóng lý thuyết hầm bà lằng của Agile</p>\n</li>\n</ul>\n<p>Ví dụ đi tập gym, bạn muốn tăng cơ giảm mỡ, trong đó bạn sẽ có rất nhiều dạng bài tập, thích tay to, chân to, ngực to, mông to,... thì có những bài tập khác nhau để chọn. <strong>Scrum</strong> là <strong>một trong những dạng bài tập như thế</strong>. <strong>Agile</strong> là quy định của phòng tập, bạn phải đến đó 3 ngày một tuần, một buổi 2 tiếng.</p>\n<p>Về nguồn gốc, <strong>Agile</strong> được sử dụng rộng rãi trong các công ty Nhật Bản từ những thập niên 70, 80 như Toyota, Fuji, Honda</p>\n<p>Vào giữa những năm 90, Jeff Sutherland cảm thấy quá bực bội vì công ty của ông ta liên tục dính những dự án trễ kế hoạch, vượt ngân sách. Anh ấy đi tìm giải pháp cho vấn đề này, đọc thấy Agile, anh bắt đầu <em>\"chế\"</em> Scrum, và nó nhanh chóng được cộng đồng công nghệ sử dụng rộng rãi.</p>\n<h1 id=\"scrum-dành-cho-ai\"><a href=\"#scrum-d%C3%A0nh-cho-ai\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Scrum dành cho ai</h1>\n<p>Nói Scrum chỉ thật sự có ích cho kỹ sư, lập trình viên thấy không đúng. Scrum còn mang đến nhiều lợi ích khác cho nhiều loại dự án khác nữa, vì ngay từ đầu người sáng tạo ra Agile không phải để dùng riêng cho ngành phần mềm.</p>\n<p>David Matthew có phát biểu: \"Nếu bạn đang làm việc trong lĩnh vực marketing, cần viết một văn bản cho dự án, sử dụng Scrum không chỉ giúp bạn cấu trúc bài viết của bạn tốt hơn, nó còn giúp các thành viên trong team hiểu vấn đề tốt hơn\"</p>\n<p>Scrum đang được dùng rộng rãi bởi FBI, các bạn làm marketing, những đội ngũ xây dựng (đang nói ở nước ngoài nhé). Miễn bạn đang cần làm một sản phẩm, một phần mềm, chiến dịch quảng cáo, Scrum có thể giúp team của bạn vận hành có tổ chức hơn, làm mọi thứ nhanh hơn.</p>\n<h1 id=\"vai-trò-và-các-phần-của-scrum\"><a href=\"#vai-tr%C3%B2-v%C3%A0-c%C3%A1c-ph%E1%BA%A7n-c%E1%BB%A7a-scrum\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Vai trò và các phần của Scrum</h1>\n<p>Bạn không cần gì nhiều để bắt đầu với Scrum, bạn chỉ một nơi để tổ chức các ý tưởng của mình, hoặc gọi là <strong>Backlog</strong>, bạn cần những người cho những vai trò khác nhau, <strong>product owner</strong>, <strong>Scrum master</strong></p>\n<ul>\n<li><strong>Product owner</strong> là người quan tâm đến sản phẩm cuối cùng, người có quyền quyết định sản phẩm cuối cùng sẽ có gì. Người này sẽ đảm nhiệm việc tạo <strong>backlog</strong> - một tập các công việc cần làm, tập các yêu cầu cần có cho sản phẩm. Yêu cầu bắt buộc của Backlog là nó phải có <strong>thứ tự ưu tiên</strong>, product owner phải quyết định cái nào trước cái nào sau. Ví dụ nếu đang chế một cái xe thì <strong>động cơ</strong> phải nằm trên mục cao nhất của yêu cầu, <strong>được sơn màu đỏ</strong> thì có thể nằm ở cuối danh sách.</li>\n<li><strong>Sprint</strong> là một khung thời gian cố định mà toàn bộ team phải xong một phần công việc được quy ước trong backlog. Thời gian dài ngắn tùy thuộc vào yêu cầu của team, 2 tuần là lựa chọn phổ biến.</li>\n<li><strong>Daily scrum</strong> là một một buổi họp hằng ngày để cập nhập tình hình công việc, còn gọi là <strong>Daily stand up</strong></li>\n<li>Kết thúc mỗi Sprint là <em>tổng kết, đánh giá cuối năm học</em>, mọi người xem lại công việc của mình, bàn luận những thứ có thể cải thiện cho Sprint sau.</li>\n</ul>\n<p>Như bạn thấy, không có gì yêu cầu đặc biệt về thiết bị hay training để bắt đầu. Phần khó nhất chỉ là phân biệt được từng khái niệm, làm đúng vai trò và đọc kỹ hướng dẫn sử dụng trước khi dùng.</p>\n<blockquote>\n<p>Giống như đánh bài, bạn chỉ tốn 10 phút để học Scrum, nhưng để chơi bài giỏi, bạn cần thời gian và trải nghiệm.</p>\n</blockquote>\n<p><a href=\"https://blog.trello.com/beginners-guide-scrum-and-agile-project-management\">Link bài gốc</a></p>","timeToRead":3,"excerpt":"Làm trong các công ty phần mềm, bạn sẽ nghe đi nghe lại mấy chữ này  Scrum ,  Agile ,  planning poker ,  Stand up ,  Sprint . Nó là những…","frontmatter":{"title":"Quản lý dự án theo Agile và Scrum cho người mới bắt đầu","cover":"https://blog.trello.com/hs-fs/hubfs/Imported_Blog_Media/scrum_agile_feature-1024x512.jpg?t=1537150324734&width=1024&height=512&name=scrum_agile_feature-1024x512.jpg","date":"2018-09-15","category":null,"tags":["ux-ui"],"desc":"Giải thích một trong những thuật ngữ rất phổ biến của quy trình phát triển phần mềm"},"fields":{"slug":"/2018-09-15-huong-dan-cho-nguoi-moi-bat-dau-voi-scrum-va-agile-project-manament"}}},"pathContext":{"slug":"/2018-09-15-huong-dan-cho-nguoi-moi-bat-dau-voi-scrum-va-agile-project-manament","prev":{"frontmatter":{"title":"Giải thích Workflow theo kiểu Gitflow","desc":"Gitflow là một dạng quy trình làm việc với Git, được giới thiệu bởi Vincent Driessen và sử dụng rất phổ biến trong các công ty phần mềm, đặt ra những quy ước trong việc tổ chức các branch trên Git","type":"post","category":null,"tags":["javascript"],"date":"2018-09-16","cover":"https://image.slidesharecdn.com/gitflow-160421170910/95/git-flow-7-638.jpg"},"fields":{"slug":"/2018-09-16-huong-dan-gitflow-workflow"}},"next":{"frontmatter":{"title":"Sử dụng Flow để check type trong React Redux","desc":"Chúng ta sẽ không nói nhiều cách sử dụng Redux trong bài này, các bạn có thể xem các bài trước, ở đây mình chỉ đi đến vấn đề đưa Flow vào để check type trong Redux","type":"post","category":null,"tags":["react","redux"],"date":"2018-09-13","cover":""},"fields":{"slug":"/2018-09-13-huong-dan-su-dung-flow-voi-react-redux"}}}}

